<template>
	<div class="report-container">
		<div class="report-title">
			<h1></h1>
		</div>
		<div class="report-content">
			<div class="my-3 row">
				<label for="reportUserName" class="form-label fw-bold fs-5"></label>
				<input type="text" class="form-control" id="reportUserName" maxlength="20" placeholder=" .." v-model="user_name">
			</div>
			<div class="my-3 row">
				<label for="reportUserEmail" class="form-label fw-bold fs-5"> ()</label>
				<input type="email" class="form-control" id="reportUserEmail" maxlength="100" placeholder=" .." v-model="user_email">
				<div id="emailHelp" class="form-text"><i class="bi bi-exclamation-triangle-fill"></i>    .    .</div>
				<div class="form-text text-danger" v-if="isNotEmail">    .</div>
			</div>
			<div class="my-3 row">
				<label for="reportTitle" class="form-label fw-bold fs-5"></label>
				<input type="text" class="form-control" id="reportTitle" maxlength="100" placeholder=" .." v-model="title">
			</div>
			<div class="my-3 row">
				<label for="reportContent" class="form-label fw-bold fs-5"></label>
				<textarea class="form-control" id="reportContent" rows="5" minlength="5" maxlength="2000" placeholder=" .." v-model="content"></textarea>
				<div id="contentLength" class="form-text text-end">{{content.length}}/2000</div>
			</div>
			<div class="mt-5 row">
				<button type="button" class="btn btn-primary btn-lg" @click="sendReport"><i class="bi bi-send-fill"></i> </button>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: "Report",
	data() {
		return {
			user_name: "",
			user_email: "",
			title: "",
			content: "",
			isNotEmail: false,
		}
	},
	watch: {
		user_email() {
			if(this.user_email) {
				this.isNotEmail = !this.$store.getters.REGEX_EMAIL.test(this.user_email);
			}
		},
		content() {

		}
	},
	methods: {
		sendReport() {
			if (localStorage.getItem("report_date") !== new Date().toDateString()) {
				localStorage.setItem("report_date", new Date().toDateString());
				localStorage.setItem("report_count", 0);
			} else {
				if (localStorage.getItem("report_count") <= 5) {
					localStorage.setItem("report_count", (+localStorage.getItem("report_count")) + 1);
				} else {
					this.$swal({ icon: "error", title: " .", text: "   .\n  .", });
					return;
				}
			}

			//  
			if (!this.validation()) return;

			let data = {
				user_name: this.user_name.trim(),
				user_email: this.user_email.trim(),
				title: this.title.trim(),
				content: this.content.trim(),
			}

			// XSS 
			for (const idx in data) {
				data[idx] = this.$sanitize(data[idx]);
			}

			// 
			this.$axios.post('/api/report', data)
			.then(res => {
				if(res.data.success) {
					this.$swal(this.$swalt("success", " !"));
					this.user_name = "";
					this.user_email = "";
					this.title = "";
					this.content = "";
				} else {
					this.$swal({ icon: "error", title: " .", text: "   .", });
				}
			})
			.catch(() => {
				this.$swal({ icon: "error", title: "", text: " .", });
			});
		},
		validation() {
			if (!this.user_name) {
				this.$swal(this.$swalt("error", "  ."));
				return false;
			}
			if (!this.user_email) {
				this.$swal(this.$swalt("error", "  ."));
				return false;
			} else if(this.isNotEmail) {
				this.$swal(this.$swalt("error", "    ."));
				return false;
			}
			if (!this.title) {
				this.$swal(this.$swalt("error", "  ."));
				return false;
			}
			if (!this.content) {
				this.$swal(this.$swalt("error", "  ."));
				return false;
			}

			return true;
		}
	}
}
</script>

<style>
	.report-container > .report-title {
		margin: 0 5px 10px 5px;
		border-bottom: 1px solid lightgray;
	}
	.report-container > .report-content {
		padding: 0 20px;
	}
	.report-container > .report-content .form-label {
		padding: 0;
	}
</style>